import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Heart, Images, Play, Store, Calendar, Clock, Users, Utensils, MoreHorizontal, Pencil, Trash2 } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { CommunityPost, useLikePost, useDeletePost } from "@/hooks/useCommunityPosts";
import { useAuth } from "@/contexts/AuthContext";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import EditPostDialog from "@/components/community/EditPostDialog";
import EditRecipeDialog from "@/components/community/EditRecipeDialog";

interface PostGridCardProps {
  post: CommunityPost;
  showAssociations?: boolean;
}

interface ExtendedPost extends CommunityPost {
  post_type?: string;
  recipe_name?: string;
  cook_time?: number;
  difficulty?: string;
  servings?: number;
}

const PostGridCard = ({ post, showAssociations = false }: PostGridCardProps) => {
  const { t } = useTranslation();
  const { user } = useAuth();
  const navigate = useNavigate();
  const { getLocalizedField } = useLocalizedContent();
  const likePost = useLikePost();
  const deletePost = useDeletePost();
  const [imageError, setImageError] = useState(false);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [showEditDialog, setShowEditDialog] = useState(false);

  const extendedPost = post as ExtendedPost;
  const isRecipe = extendedPost.post_type === 'recipe';
  const isAuthor = user?.id === post.author_id;

  const handleLike = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (!user) return;
    likePost.mutate({ postId: post.id, liked: post.user_liked || false });
  };

  const handleCardClick = () => navigate(`/community/${post.id}`);
  const handleRestaurantClick = (e: React.MouseEvent, id: string) => { e.preventDefault(); e.stopPropagation(); navigate(`/restaurant/${id}`); };
  const handleActivityClick = (e: React.MouseEvent) => { e.preventDefault(); e.stopPropagation(); if (post.activity?.id) navigate(`/activity/${post.activity.id}`); };

  const handleDelete = () => {
    deletePost.mutate(post.id);
    setShowDeleteDialog(false);
  };

  const getPlainTextContent = (html: string) => new DOMParser().parseFromString(html, "text/html").body.textContent || "";

  const content = getLocalizedField(post, "content");
  const plainContent = getPlainTextContent(content);
  const hasImages = post.image_urls && post.image_urls.length > 0;
  const hasVideo = !!post.video_url;
  const hasMedia = hasImages || hasVideo;
  const imageCount = post.image_urls?.length || 0;

  const allRestaurants = [...(post.restaurants || []), ...(post.restaurant && !post.restaurants?.find(r => r.id === post.restaurant?.id) ? [post.restaurant] : [])];
  const activityTitle = post.activity ? getLocalizedField(post.activity, "title") : null;
  const maxVisibleRestaurants = 2;

  const getDifficultyColor = (d: string) => {
    switch (d) {
      case 'easy': return 'bg-green-500/20 text-green-700 dark:text-green-400';
      case 'medium': return 'bg-yellow-500/20 text-yellow-700 dark:text-yellow-400';
      case 'hard': return 'bg-red-500/20 text-red-700 dark:text-red-400';
      default: return 'bg-muted text-muted-foreground';
    }
  };

  const getDifficultyLabel = (d: string) => {
    switch (d) {
      case 'easy': return t("recipe.difficulty.easy", "简单");
      case 'medium': return t("recipe.difficulty.medium", "中等");
      case 'hard': return t("recipe.difficulty.hard", "困难");
      default: return d;
    }
  };

  return (
    <>
      <div className="group cursor-pointer rounded-lg overflow-hidden bg-card border border-border/50 hover:shadow-lg transition-all duration-300 hover:-translate-y-1 relative" onClick={handleCardClick}>
        {/* Author Menu */}
        {isAuthor && (
          <div className="absolute top-2 right-2 z-20" onClick={(e) => e.stopPropagation()}>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <button className="h-7 w-7 rounded-full bg-black/40 backdrop-blur-sm flex items-center justify-center hover:bg-black/60 transition-colors">
                  <MoreHorizontal className="h-4 w-4 text-white" />
                </button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="min-w-[120px]">
                <DropdownMenuItem onClick={() => setShowEditDialog(true)}>
                  <Pencil className="h-4 w-4 mr-2" />
                  {t("common.edit", "编辑")}
                </DropdownMenuItem>
                <DropdownMenuItem className="text-destructive focus:text-destructive" onClick={() => setShowDeleteDialog(true)}>
                  <Trash2 className="h-4 w-4 mr-2" />
                  {t("common.delete", "删除")}
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        )}

        {/* Media Section */}
        {hasMedia && (
          <div className={`relative ${isRecipe ? 'aspect-[4/3]' : 'aspect-[4/5]'} overflow-hidden`}>
            {hasImages ? (!imageError ? (
              <img src={post.image_urls![0]} alt="" className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500" onError={() => setImageError(true)} />
            ) : (
              <div className="w-full h-full bg-muted flex items-center justify-center"><span className="text-xs text-muted-foreground">{t("common.imageLoadError", "图片加载失败")}</span></div>
            )) : hasVideo ? (
              <video src={post.video_url} className="w-full h-full object-cover" preload="metadata" muted playsInline />
            ) : null}
            
            {isRecipe && <Badge className="absolute top-2 left-2 bg-primary text-primary-foreground gap-1"><Utensils className="h-3 w-3" />{t("recipe.badge", "食谱")}</Badge>}
            {hasVideo && !isRecipe && <div className="absolute inset-0 flex items-center justify-center pointer-events-none"><div className="w-12 h-12 rounded-full bg-black/50 backdrop-blur-sm flex items-center justify-center"><Play className="h-5 w-5 text-white fill-white ml-0.5" /></div></div>}
            {!hasVideo && imageCount > 1 && !isRecipe && <div className={`absolute top-2 ${isAuthor ? 'right-11' : 'right-2'} bg-black/60 text-white text-xs px-2 py-0.5 rounded-full z-10 flex items-center gap-1`}><Images className="h-3 w-3" /><span>{imageCount}</span></div>}
          </div>
        )}

        {!hasMedia && isRecipe && (
          <div className="relative aspect-[4/3] overflow-hidden bg-gradient-to-br from-primary/20 to-primary/5 flex items-center justify-center">
            <Utensils className="h-12 w-12 text-primary/40" />
            <Badge className="absolute top-2 left-2 bg-primary text-primary-foreground gap-1"><Utensils className="h-3 w-3" />{t("recipe.badge", "食谱")}</Badge>
          </div>
        )}

        <div className="p-3">
          {isRecipe && (
            <div className="flex items-center gap-3 text-xs text-muted-foreground mb-2">
              {extendedPost.cook_time && <div className="flex items-center gap-1"><Clock className="h-3.5 w-3.5" /><span>{extendedPost.cook_time}{t("recipe.min", "分")}</span></div>}
              {extendedPost.servings && <div className="flex items-center gap-1"><Users className="h-3.5 w-3.5" /><span>{extendedPost.servings}{t("recipe.servingsShort", "人")}</span></div>}
              {extendedPost.difficulty && <Badge variant="secondary" className={`text-xs px-1.5 py-0 h-5 ${getDifficultyColor(extendedPost.difficulty)}`}>{getDifficultyLabel(extendedPost.difficulty)}</Badge>}
            </div>
          )}

          {showAssociations && (allRestaurants.length > 0 || activityTitle) && (
            <div className="flex flex-wrap gap-1.5 mb-2">
              {allRestaurants.slice(0, maxVisibleRestaurants).map((restaurant) => (
                <Badge key={restaurant.id} variant="secondary" className="text-xs cursor-pointer hover:bg-secondary/80 gap-1" onClick={(e) => handleRestaurantClick(e, restaurant.id)}>
                  <Store className="h-3 w-3" /><span className="truncate max-w-[120px]">{getLocalizedField(restaurant, "name")}</span>
                </Badge>
              ))}
              {allRestaurants.length > maxVisibleRestaurants && <Badge variant="secondary" className="text-xs gap-1">+{allRestaurants.length - maxVisibleRestaurants}</Badge>}
              {activityTitle && <Badge variant="secondary" className="text-xs cursor-pointer hover:bg-secondary/80 gap-1" onClick={handleActivityClick}><Calendar className="h-3 w-3" /><span className="truncate max-w-[120px]">{activityTitle}</span></Badge>}
            </div>
          )}

          <p className={`text-sm font-medium line-clamp-2 leading-relaxed ${hasMedia ? '' : 'min-h-[60px]'}`}>{isRecipe ? extendedPost.recipe_name : plainContent}</p>

          {/* Recipe food introduction */}
          {isRecipe && plainContent && !plainContent.includes("主要配料：") && (
            <p className="text-xs text-muted-foreground line-clamp-3 leading-relaxed mt-1">{plainContent}</p>
          )}

          <div className="flex items-center justify-between mt-3">
            <div className="flex items-center gap-2 min-w-0 flex-1">
              <Avatar className="h-5 w-5 flex-shrink-0"><AvatarImage src={post.author?.avatar_url || undefined} /><AvatarFallback className="text-[10px]">{post.author?.display_name?.charAt(0) || "?"}</AvatarFallback></Avatar>
              <span className="text-xs text-muted-foreground truncate">{post.author?.display_name}</span>
            </div>
            <button className={`flex items-center gap-1 text-xs ${post.user_liked ? "text-red-500" : "text-muted-foreground"} hover:text-red-500 transition-colors`} onClick={handleLike} disabled={!user}>
              <Heart className={`h-4 w-4 ${post.user_liked ? "fill-current" : ""}`} /><span>{post.likes_count || 0}</span>
            </button>
          </div>
        </div>
      </div>

      {/* Delete Confirmation */}
      <AlertDialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("community.deleteConfirmTitle", "确认删除")}</AlertDialogTitle>
            <AlertDialogDescription>{t("community.deleteConfirmDesc", "删除后将无法恢复，确定要删除这篇帖子吗？")}</AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>{t("common.cancel", "取消")}</AlertDialogCancel>
            <AlertDialogAction onClick={handleDelete} className="bg-destructive text-destructive-foreground hover:bg-destructive/90">{t("common.delete", "删除")}</AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Edit Dialog */}
      {isRecipe ? (
        <EditRecipeDialog post={post} open={showEditDialog} onOpenChange={setShowEditDialog} />
      ) : (
        <EditPostDialog post={post} open={showEditDialog} onOpenChange={setShowEditDialog} />
      )}
    </>
  );
};

export default PostGridCard;
